<script>
export default {
  data() {
    return {
      fab: false,
    };
  },
  methods: {
    onScroll() {
      this.fab = window.scrollY > 300;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
  },
};
</script>

<template>
  <v-btn
    v-show="fab"
    v-scroll="onScroll"
    fab
    dark
    fixed
    bottom
    right
    color="primary"
    @click="toTop"
  >
    <v-icon>mdi-chevron-up</v-icon>
  </v-btn>
</template>
