<script>
import CAuthor from '../components/CAuthor';
import CSearch from '../components/CSearch';
import CRadios from '../components/CRadios';

export default {
  components: {
    CAuthor,
    CSearch,
    CRadios,
  },
  data() {
    return {
      apiData: [],
      searchList: [],
      followList: [],
      sortMode: 'date',
    };
  },
  computed: {
    sortData() {
      let copyData = [...this.apiData];
      if (this.sortMode === 'date') {
        return copyData.sort((a, b) => this.formatTime(b.updateTime) - this.formatTime(a.updateTime));
      }
      if (this.sortMode === 'article') {
        return copyData.sort((a, b) => b.blogList.length - a.blogList.length);
      }
      if (this.sortMode === 'follow') {
        return this.apiData.filter((item) => item.follow);
      }
      return this.apiData;
    },
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      const api = 'https://raw.githubusercontent.com/hexschool/w3hexschool-API/master/data.json';
      this.axios.get(api).then((res) => {
        this.apiData = [...res.data];
        this.setFollow();
        this.setSearchList();
      });
    },
    setFollow() {
      const list = JSON.parse(localStorage.getItem('followList'));
      if (list) {
        this.followList = list;
      }

      this.apiData.forEach((item) => {
        if (this.followList.includes(item.keyID)) {
          item.follow = true;
        } else {
          item.follow = false;
        }
      });
    },
    setSearchList() {
      // get all article
      this.apiData.forEach((item) => {
        let temp = [...item.blogList];
        temp.forEach((obj) => {
          obj.name = item.name;
        });
        this.searchList.push(...temp);
      });
    },
    getSortMode(mode) {
      this.sortMode = mode;
    },
    formatTime(updateTime) {
      const temp = updateTime.split(' ');
      const time = temp[2].split(':');
      const hour = (time[0] % 12) * 60 * 60 * 1000;
      const min = time[1] * 60 * 1000;
      const sec = time[2] * 1000;
      let halfDay = temp[1] === '下午' ? 12 * 60 * 60 * 1000 : 0;
      const sum = hour + min + sec + halfDay;
      let date = new Date(temp[0]);
      date = new Date(date.getTime() + sum);
      return date;
    },
  },
};
</script>

<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <c-search :search-list="searchList"></c-search>
      </v-col>
    </v-row>
    <v-row align="center" justify="space-between">
      <v-col>
        <c-radios :sort-mode="sortMode" @get-mode="getSortMode"></c-radios>
      </v-col>
    </v-row>
    <v-row>
      <v-col v-for="item of sortData" :key="item.keyID" cols="12" md="6" lg="4">
        <c-author :author-data="item"></c-author>
      </v-col>
    </v-row>
  </v-container>
</template>
