<script>
export default {
  props: ['authorData'],
  data() {
    return {
      page: 1,
      pageList: [],
      follow: this.authorData.follow,
      pageCount: 5,
    };
  },
  watch: {
    follow() {
      this.authorData.follow = this.follow;

      const list = JSON.parse(localStorage.getItem('followList')) || [];
      const id = this.authorData.keyID;
      const hasId = list.includes(id);
      if (this.follow && !hasId) {
        list.push(id);
      } else if (!this.follow && hasId) {
        list.splice(list.indexOf(id), 1);
      }
      localStorage.setItem('followList', JSON.stringify(list));
    },
    pageCount: {
      immediate: true,
      handler() {
        this.setPages();
      },
    },
  },

  methods: {
    setPages() {
      this.pageList = [];
      const copy = [...this.authorData.blogList].reverse();
      let temp = [];
      for (let i = 0, n = copy.length; i < n; i += 1) {
        if (i !== 0 && i % this.pageCount === 0) {
          this.pageList.push(temp);
          temp = [];
        }
        temp.push(copy[i]);
      }
      if (temp.length > 0) {
        this.pageList.push(temp);
      }
    },
  },
};
</script>

<template>
  <v-card :height="'100%'" :outlined="!follow" class="d-flex flex-column">
    <v-card-title class="justify-space-between align-end">
      <a :href="authorData.blogUrl" target="_blank" title="blog">
        <h2>{{authorData.name || '匿名'}}</h2>
      </a>
      <v-switch v-model="follow" hide-details inset></v-switch>
    </v-card-title>
    <v-card-subtitle>{{authorData.updateTime}}</v-card-subtitle>
    <v-list dense shaped class="flex-grow-1">
      <v-subheader>文章列表 {{authorData.blogList.length}}</v-subheader>
      <v-list-item
        v-for="(article, index) in pageList[page - 1]"
        :key="index"
        :href="article.url"
        target="_blank"
      >
        <v-list-item-icon>
          <v-icon>mdi-file-document</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title v-text="article.title"></v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <v-card-actions>
      <v-pagination v-model="page" :length="pageList.length" :total-visible="6"></v-pagination>
    </v-card-actions>
  </v-card>
</template>


