<script>
export default {
  props: ['sortMode'],
  data() {
    return {
      radios: [
        {
          label: '近期更新',
          value: 'date',
        },
        {
          label: '文章數量',
          value: 'article',
        },
        {
          label: '追蹤作者',
          value: 'follow',
        },
      ],
      mode: this.sortMode,
    };
  },
  watch: {
    mode(val, oldVal) {
      if (val !== oldVal) {
        this.$emit('get-mode', this.mode);
      }
    },
  },
};
</script>

<template>
  <v-radio-group v-model="mode" row dense>
    <v-radio v-for="r in radios" :key="r.label" :label="r.label" :value="r.value"></v-radio>
  </v-radio-group>
</template>